.breeds {
   background-color: var(--gray);
   padding: 18px 30px 50px;
   border-bottom-left-radius: 46px;
   border-bottom-right-radius: 46px;
}

.subheading {
   font-weight: 500;
}

.subheading > span {
   display: flex;
   flex-direction: column;
   gap: 1rem;
   width: 40px;
   height: 3px;
   background: var(--brown);
   margin-top: 8px;
}

.heading {
   width: 185px;
   padding: 16px 0 26px;
}

.heading > h2 {
   font-size: 18px;
   font-weight: 700;
   text-align: left;
   color: var(--brown-heading);
}

.heading > a {
   display: none;
}

@media screen and (min-width: 1024px) {
   .breeds {
      background-color: var(--gray);
      padding: 46px 108px 98px;
      border-bottom-left-radius: 46px;
      border-bottom-right-radius: 46px;
   }

   .subheading > span {
      width: 60px;
   }

   .heading {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      width: auto;
      padding: 36px 0 46px;
   }

   .heading > h2 {
      width: 536px;
      font-size: 48px;
      line-height: 59px;
   }

   .heading > a {
      display: initial;
   }

   .heading > button {
      display: flex;
   }
}
